import React, { Component } from 'react';
import { Row, Col, Card, Divider } from 'antd';
import { BtnSave, BtnSearch, BtnClear } from 'components/common/button';
import TableWarningSelected from './TableWarningSelected';
import TableWarningNotSelected from './TableWarningNotSelected';

class TabAcademicWarning extends Component {
  state = {};
  render() {
    return (
      <Card>
        <Divider>Danh sch sinh vin cha chn</Divider>
        <Row>
          <Col span={12}>
            <div>
              S sinh vin cnh bo thi hc: <b>0</b>
            </div>
          </Col>
          <Col span={12} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableWarningNotSelected />
          </Col>
        </Row>
        <Divider>Danh sch sinh vin  chn</Divider>
        <Row>
          <Col span={12}>
            <div>
              Tng s sinh vin: <b>0</b>
            </div>
          </Col>
          <Col span={12} style={{ textAlign: 'right' }}>
            <BtnSave />
          </Col>
        </Row>
        <Row className="rowMargin">
          <Col span={24}>
            <TableWarningSelected />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default TabAcademicWarning;



