import React, { Component } from 'react';
import { Table, Button } from 'antd';
import { setStudentWarningAndStopSchool } from 'dump/data';

class TableAcademicWarning extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'M sinh vin',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H v tn',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dayOfBirth',
        key: 'dayOfBirth',
      },
      {
        title: 'Tn lp',
        dataIndex: 'class',
        key: 'class',
      },
      {
        title: 'TBCHK trc',
        dataIndex: 'tBCHKBefore',
        key: 'tBCHKBefore',
      },
      {
        title: 'TBCHK sau',
        dataIndex: 'tBCHKAfter',
        key: 'tBCHKAfter',
      },
      {
        title: 'TBCTL',
        dataIndex: 'tBCTL',
        key: 'tBCTL',
      },
      {
        title: 'Nm o to',
        dataIndex: 'yearsOfTraining',
        key: 'yearsOfTraining',
      },
      {
        title: 'L do',
        dataIndex: 'reason',
        key: 'reason',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: () => (
          <Button className="btnSmallDeleteInTable" onClick={this.showModal}>
            Xa
          </Button>
        ),
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setStudentWarningAndStopSchool}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableAcademicWarning;



