import React, { Component } from 'react';
import { Row, Col, Menu, Icon } from 'antd';
import './style.scss';

class MenuTabTop extends Component {
  state = {};

  handleClick = e => {
    // console.log('click ', e);
    this.setState({
      current: e.key,
    });
    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };
  render() {
    return (
      <Row>
        <Col span={24}>
          <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
            <Menu.Item key="considerAuto">
              <Icon type="setting" />
              Tng hp
            </Menu.Item>
            <Menu.Item key="enteringInfo">
              <a href="#student/WarningAndForcedStopSchool/add">
                <Icon type="highlight" theme="twoTone" twoToneColor="#ff3333" />
                Nhp Q thi hc, ngng hc
              </a>
            </Menu.Item>
            <Menu.Item key="printAcademicWarning">
              <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" />
              In cnh bo hc tp
            </Menu.Item>
            <Menu.Item key="printStopSchool">
              <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" />
              In xt thi hc
            </Menu.Item>
            <Menu.Item key="fileExcel">
              <Icon type="file-excel" theme="twoTone" twoToneColor="#00cc00" />
              Xut Excel
            </Menu.Item>
          </Menu>
        </Col>
      </Row>
    );
  }
}

export default MenuTabTop;



